/* blue - color: #2C5AB4; */
/* gray - color: #5F5F5F */
@import url('https://fonts.googleapis.com/css2?family=Inter&family=Rubik+Iso&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Gloock&display=swap');

html, body {
  max-width: 100vw;
  overflow-x: hidden !important;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Inter', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: white !important;
  overflow-y: scroll;
  position:relative;
}
/*header*/
*{
  box-sizing: border-box;

}

:root {
	--mainColor: white;
	--mainColorLight: #5f5f5f;
	--blue: #2C5AB4;
	--gray: #5f5f5f;;
}

header {
	display:  flex;
	align-items:  center;
	justify-content: space-between;
	height: 100%;
  display: flex;
  padding: 0 2rem;
	color: var(--gray);
  width: 85%;
  margin: auto;
}
 
.logo-container {
  padding: 15px;
}


.nav-container{
  display: flex;
  width: 40%;
  text-align: end;
  padding:0;
  justify-content:flex-end;

}
  nav a {
    margin: 0 1rem;
    color: var(--gray);
    text-decoration: none;

  }

nav a:hover {
	color: black;
}

header .nav-btn {
	padding: 5px;
	cursor: pointer;
	background: transparent;
  display:none;
	border: none;
	outline: none;
	color: var(--textColor);
	visibility: hidden;
	opacity: 0;
	font-size: 1.8rem;
 
 
}

header div,
nav {
	display: flex;
	align-items: center;
}

@media only screen and (max-width: 768px) {
  
  header{
    width: 100%;
    justify-content: space-between;
    
  }
  
	header .nav-btn {
    display: flex;
		visibility: visible;
		opacity: 1;
    
	}

	header nav {
		position: fixed;
		top: -100vh;
		left: 0;
		height: 100%;
		width: 100%;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		gap: 1.5rem;
		background-color: var(--blue);
		transition: 1s;
    transform: translateY(-100vh);
    color:white;
	}

	header .responsive_nav {
		transform: translateY(100vh);
    display: flex;
	}
  nav a:hover {
    color: rgb(237, 216, 81);
  }
  

	nav .nav-close-btn {
		position: absolute;
		top: 2rem;
		right: 2rem;
	}

	nav a {
		font-size: 1.5rem;
    color: white;
	}
 
}
 

/*--------------------------content------------------------------*/
/* greeting */
.greeting-container {
  width: 40%;
  margin: auto;
  max-width: 100vw;
  padding-left:0% ;
  text-align: start;
}
@media only screen and (max-width: 768px) {

