   width:50%;
   margin: auto;
  }
 
 }
 
 /*rotating text*/ 
.im{
  font-size:50px;
}
.inner{
  position: relative;
  height:60px;
  line-height:60px;
  font-size:50px;
  overflow:hidden;
  display:inline-block;
  align-items: center;
  vertical-align: top;
}
.inner span{
  animation:animate 10s ease infinite;
  position:relative;
  color:#e58e26;
   
}
@keyframes animate{
  0%,100%{
    top:0;
  }
  20%{
    top:0px;
  }  
  25%{
    top:-60px;
  }
  45%{
    top:-60px;
  }
  50%{
    top:-120px;
  }
  70%{
    top:-120px;
  }
  75%{
    top:-120px;
  }
  95%{
    top: -120px;
  }
}

@media only screen and (max-width: 768px) {
.outer{
  display: flex;
  align-items: center;
  
}
 
 .im{
  font-size: 30px;
  text-align: start;
  align-items: center;
  vertical-align: top;
  display: inline-block; 
 }

.inner{
  position: relative;
  height:40px;
  line-height:40px;
  font-size:30px;
  overflow:hidden;
  display:inline-block;
  align-items: center;
  vertical-align: top;
  text-align: start;
  max-width: 100%;
  z-index: -1;

 
}
@keyframes animate{
  0%,100%{
    top:0;
  }
  20%{
    top:0px;
  }  
  25%{
    top:-40px;
  }
  45%{
    top:-40px;
  }
  50%{
    top:-80px;
  }
  70%{
    top:-80px;
  }
  75%{
    top:-120px;
  }
  95%{
    top:-120px;
  }
}


}

.location-container{
  display: flex;
}

.location{
   color: grey !important;
   font-size: 24px !important;
   font-weight: 450;
    
