/*work and play buttons*/
button {
  margin: 0 5px;
  padding: 10px;
  background-color: #ccc;
  border: none;
  border-radius: 5px;
  font-size: 16px;
}

.banner {
  width: 70%;
  margin:  auto;

}
 


.projects {
  width: 100%;
  display: block;
}

.button-container {
  padding: 10px;
  justify-content: space-between;
  margin: auto;
  width: 100%;
  text-align: center;
  
}

.work {
  display: inline-block;
  padding: 20px 20px;
  border-radius: 35px;
  background-image: linear-gradient(to right, #29323c, #485563, #2b5876, #4e4376);
  box-shadow: 0 4px 15px 0 rgba(45, 54, 65, 0.75);
  border: none;
  color: white !important;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.3);
  cursor: pointer;
  font-size: 20px;
  text-align: center;
  text-decoration: none;
  transition: transform 0.2s ease-in-out;
  width: 100%;
  font-weight: 500;
  margin-top: 10px;
  max-width: 40%;
   


}

.play {
  
  background: rgba(59, 173, 227, 1);
  background: linear-gradient(45deg, rgba(59, 173, 227, 1) 0%, rgba(87, 111, 230, 1) 25%, rgba(152, 68, 183, 1) 51%, rgba(255, 53, 127, 1) 100%);
  display: inline-block;
  padding: 20px 20px;
  font-weight: 500;
  border-radius: 35px;
  box-shadow: 0 4px 15px 0 rgba(45, 54, 65, 0.75);
  border: none;
  color: white !important;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.3);
  cursor: pointer;
  font-size: 22px;
  text-align: center;
  text-decoration: none;
  transition: transform 0.2s ease-in-out;
  width: 100%;
  max-width: 40%;
  margin-top: 10px;

}


.work:hover,
.play:hover {
  transform: scale(1.1);

}

@media only screen and (max-width: 768px) {

   .play,.work{
     text-align: center;
     width: 100%;
     text-decoration: none;
     margin-top: 10px;
   }


}



